.important-message {
	width: 100%;
	min-height: 50px;
	background-color: #cb655e;
	padding-top: 0.6rem;
}
.home-hero {
	text-shadow: 3px 3px #fff;
	position: relative;
	min-height: 500px;
	width: 100%;
	background-color: #ccc;
	background-image: url('../../../../uploads/2020/09/livingston-front-scaled.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 25%;
}
.about-livingston {
	width: 100%;
	min-height: 200px;
	background: #d0e6ff;
}
.tagline {
	position: absolute;
	bottom: 0;
	margin-bottom: 15px;
	right: 0;
	margin-right: 15px;
	float: right;
	text-shadow: 3px 3px #000;
}
.injured {
	min-height: 200px;
	background-color: #fff;
	width: 100%;
}
.below-knee {
	font-size: 3.3rem;
	color: #facd62;
}
.btn-blue {
	background: #d0e6ff;
}
.choice {
	width: 100%;
	min-height: 200px;
	background: #b0b0b0;
	background-image: url('../../../../uploads/2020/09/ankle.png');
	background-repeat: no-repeat;
	background-position: right;
	background-size: contain;
}
.office {
	width: 100%;
	min-height: 200px;
	background: #d0e6ff;
}
.whats-new {
	background: #fff;
	padding: 55px;
}
.footer {
	background: #fff;
	width: 100%;
	min-height: 200px;
}
